@use './mixin/mixin' as *;
@use './setting/setting' as *;
@use './setting/normalize' as *;


.recruite{
  max-width: 960px;
  margin: 100px auto 105px;
  padding: 0 20px;
}

.recruite_title{
  font-size: 32px;
  line-height: lh(48, 32);
}

.accordion{
  margin-top: 50px;
}

.accordion_item{
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  &:nth-of-type(2) {
    border: none;
  }
  &.is-accordion_item {
    .accordion_headline {
      background-color: #FFDA5F;
      &::before {
        transform: rotate(180deg);
      }
      &::after {
        opacity: 0;
      }
    }
    .accordion_inner {
      max-height: 102vh;
      padding: 35px 0 75px;
    }
  }
}

.accordion_headline{
  font-size: 18px;
  font-weight: bold;
  line-height: lh(27, 18);
  padding: 40px 0;
  display: flex;
  align-items: center;
  position: relative;
  transition: background-color 0.5s;
  @include pc {
    padding-left: 30px;
  }
  &:hover {
    @include pc {
      background-color: #FFDA5F;
      cursor: pointer;
    }
  }
  &::before,
  &::after {
    content: "";
    height: 1px;
    width: 20px;
    background-color: #000;
    position: absolute;
    right: 35px;
  }
  &::before {
    transform: rotate(90deg);
    transition: transform 0.5s;
  }
}

.accordion_inner{
  padding: 0;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.5s, padding 0.5s;
  @include pc {
    display: flex;
    flex-wrap: wrap;
    align-items: baseline;
  }
}

.accordion_title{
  font-size: 16px;
  font-weight: bold;
  line-height: lh(24, 16);
  @include pc {
    width: 19%;
    margin-left: 30px;
  }
}

.accordion_description{
  margin-top: 10px;
  font-size: 16px;
  line-height: lh(24, 16);
  @include pc {
    width: 75%;
    margin: 0;
  }
  & + .accordion_title {
    margin-top: 40px;
  }
}
